<template>
  <transition name="leftin">
    <div class="food"
         v-show="showFlag"
         ref="food">
      <div class="food-content">
        <div class="image-header">
          <img :src="food.image">
          <div class="back" @click="hide">
            <i class="icon-arrow_lift"></i>
          </div>
        </div>
        <div class="content">
          <h1 class="title">{{food.name}}</h1>
          <div class="detail">
            <span class="count">月售{{food.sellCount}}份</span>
            <span class="rating">好评率{{food.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">￥{{food.price}}</span>
            <span class="old" v-if="food.oldPrice">￥{{food.oldPrice}}</span>
          </div>
          <div class="cartcontral-wrapper">
            <cartcontral :food="food"></cartcontral>
          </div>
          <transition name="fade">
            <div
              @click.stop.prevent="addFoodFirst"
              class="buy"
              v-show="!food.count || food.count===0">加入购物车
            </div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <div class="info" v-show="food.info">
          <h1 class="title">商品信息</h1>
          <p class="text">{{food.info}}</p>
        </div>
        <split></split>
        <div class="rating">
          <h1 class="title">商品评价</h1>
          <select-rating
            :select-type="selectType"
            :only-content="onlyContent"
            :desc="desc"
            :ratings="food.ratings"
          >
          </select-rating>
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li
                v-show="needShow(rating.rateType,rating.text)"
                v-for="rating in food.ratings"
                class="rating-item">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img class="avatar" width="12" height="12" :src="rating.avatar">
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <p class="text">
                  <span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
                </p>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<style lang="sass" rel="stylesheet/sass">
  @import '../../common/style/mixin'
  .food
    position: fixed
    left: 0
    top: 0
    bottom: 48px
    z-index: 30
    width: 100%
    background-color: #fff
    /* 从左侧飞入动画 */
    &.leftin-enter-active, &.leftin-leave-active
      transition: all 0.4s ease
      transform: translate3d(0, 0, 0)
    &.leftin-enter, &.leftin-leave-active
      transform: translate3d(100%, 0, 0)
    .image-header
      position: relative
      width: 100%
      /*  height为0
          padding-top当设置为百分百时基于width计算
          .image-header盒模型既有了可以让子节点继承的宽高
          解决了头部图片自适应不同大小屏幕时的抖动现象
        */

      height: 0
      padding-top: 100%
      img
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        border: 0
      .back
        position: absolute
        top: 10px
        left: 0
        .icon-arrow_lift
          display: block
          padding: 10px
          font-size: 20px
          color: #fff
    .content
      position: relative
      padding: 18px
      .title
        font-size: 14px
        font-weight: 700
        color: rgb(7, 17, 27)
        line-height: 14px
      .detail
        line-height: 10px
        font-size: 0
        margin-top: 8px
        color: rgb(147, 153, 159)
        span
          font-size: 10px
        .count
          margin-right: 12px
      .price
        margin-top: 8px
        font-size: 0
        line-height: 24px
        .now
          margin-right: 12px
          font-size: 14px
          color: rgb(240, 20, 20)
          font-weight: 700
        .old
          font-size: 10px
          color: rgb(147, 153, 159)
          font-weight: 700
      .cartcontral-wrapper
        position: absolute
        right: 12px
        bottom: 12px
      .buy
        position: absolute
        right: 18px
        bottom: 18px
        z-index: 10
        height: 24px
        line-height: 24px
        padding: 0 12px
        box-sizing: border-box
        border-radius: 12px
        font-size: 10px
        color: #fff
        background: rgb(0, 160, 220)
        &.fade-enter-active, &.fade-leave-active
          transition: all 0.2s
        &.fade-enter, &.fade-leave
          opacity: 0
          z-index: -1
    .info
      padding: 18px
      .title
        line-height: 14px
        margin-bottom: 6px
        font-size: 14px
        color: rgb(7, 17, 27)
      .text
        line-height: 24px
        padding: 0 8px
        font-size: 12px
        color: rgb(77, 85, 93)
    .rating
      padding-top: 18px
      .title
        line-height: 14px
        margin-left: 18px
        font-size: 14px
        color: rgb(7, 17, 27)
      .rating-wrapper
        padding: 0 18px
        .rating-item
          position: relative
          padding: 16px 0
          @include border-1px(rgba(7, 17, 27, 0.1))
          .user
            position: absolute
            right: 0
            top: 16px
            line-height: 12px
            font-size: 0
            .name
              display: inline-block
              margin-right: 6px
              vertical-align: top
              font-size: 10px
              color: rgb(147, 153, 159)
            .avatar
              border-radius: 50%
          .time
            margin-bottom: 6px
            line-height: 12px
            font-size: 10px
            color: rgb(147, 153, 159)
          .text
            line-height: 16px
            font-size: 12px
            color: rgb(7, 17, 27)
            .icon-thumb_up, .icon-thumb_down
              margin-right: 4px
              line-height: 16px
              font-size: 12px
            .icon-thumb_up
              color: rgb(0, 160, 220)
            .icon-thumb_down
              color: rgb(147, 153, 159)

        .no-rating
          padding: 16px 0
          font-size: 12px
          color: rgb(147, 153, 159)
</style>
<script type="text/ecmascript-6">
  import Vue from 'Vue';
  import BScroll from 'better-scroll';
  import cartcontral from 'components/cartcontral/cartcontral';
  import selectRating from 'components/selectRating/selectRating';
  import split from 'components/split/split';
  import {eventHub} from 'common/js/eventHub.js';
  import {formatDate} from 'common/js/date';

  // 评论选项卡默认选择全部
  const ALL = 2;
  export default{
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {
        // 组件是否显示
        showFlag: false,
        selectType: ALL,
        onlyContent: true,
        desc: {
          all: '全部',
          positive: '推荐',
          negative: '吐槽'
        }
      };
    },
    methods: {
      show() {
        this.showFlag = true;
        this.$nextTick(() => {
          if (!this.scroll) {
            this.scroll = new BScroll(this.$refs.food, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      hide() {
        this.showFlag = false;
      },
      // 产品详情页点击购物车添加商品
      addFoodFirst(event) {
        if (!event._constructed) {
          return;
        }
        eventHub.$emit('add-food', event.target);
        Vue.set(this.food, 'count', 1);
      },
      selectedType(type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      contentToggle(onlyContent) {
        this.onlyContent = onlyContent;
         this.$nextTick(() => {
          this.scroll.refresh();
         });
      },
      needShow(type, text) {
        if (this.onlyContent && !text) {
          return false;
        }
        if (this.selectType === ALL) {
          return true;
        } else {
          return type === this.selectType;
        }
      }
    },
    created() {
      eventHub.$on('select-Type', this.selectedType);
      eventHub.$on('content-Toggle', this.contentToggle);
    },
    beforeDestroy() {
      eventHub.$off('select-Type', this.selectedType);
      eventHub.$off('content-Toggle', this.contentToggle);
    },
    filters: {
      formatDate(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {cartcontral, split, selectRating}
  };
</script>



